n    font: {\n        family: 'Fredericka the Great';\n        color: $main-dark;\n        size: 64px;\n    }\n}\n\nh2, h3, h4 {\n    font-family: 'TerraIgnota';\n    color: $main-dark;\n}\n\n.wrapper {\n    background-image: url('../media/icons/oldtime_map_background.jpg');\n    background-size: cover;\n    min-height: 100vh;\n    overflow: hidden;\n}\n\nhtml, body {\n    margin: 0;\n    min-height: 100%;\n}\n\n* {\n    box-sizing: border-box;\n    border-radius: 6px;\n}\n\n// ----- PRINT FORMATTING ----- //\n// @media print {\n//     .section-container {\n//                 border: fuchsia;\n//                 color: fuchsia;\n//             }\n\n//     header, footer, form {\n//         display: none;\n//     }\n// }\n\n// @page {\n//     margin: 2cm;\n// }","@import 'base'; \n\n.section-container_weather {\n    grid-area: wr;\n    background-color: $trans-background_green;\n\n    .current-weather {\n        background-color: $trans-background_aqua;\n        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.8);\n        text-align: center;\n    }\n\n    #forecast {\n        display: flex;\n        padding: 1em;\n        border-radius: 2px;\n        background-color: rgba(255, 102, 0, 0.1);\n        justify-content: space-between;\n        text-align: center;\n        overflow: scroll;\n\n            li {\n                border-radius: 2px;\n                background-color: $main-light;\n                width: 12vw;\n                box-shadow: $parchment-box-shadow;\n            }\n\n            #btm-weather-cell {\n                min-height: 12vh;\n            }\n    }\n}"],"sourceRoot":""}]);
