 //\n$parchment-box-shadow: 1px 1px 12px 1px rgba(128, 128, 128, 0.4);\n\n// ----------- TYPOGRAPHY -----------//\n:root {\n    font: {\n        family: 'TerraIgnota', sans-serif;\n        size: 18px;\n    }\n}\n\nh1 {\n    font: {\n        family: 'Fredericka the Great';\n        color: $main-dark;\n        size: 64px;\n    }\n}\n\nh2, h3, h4 {\n    font-family: 'TerraIgnota';\n    color: $main-dark;\n}\n\n.wrapper {\n    background-image: url('../media/icons/oldtime_map_background.jpg');\n    background-size: cover;\n    min-height: 100vh;\n    overflow: hidden;\n}\n\nhtml, body {\n    margin: 0;\n    min-height: 100%;\n}\n\n* {\n    box-sizing: border-box;\n    border-radius: 6px;\n}\n\n// ----- PRINT FORMATTING ----- //\n// @media print {\n//     .section-container {\n//                 border: fuchsia;\n//                 color: fuchsia;\n//             }\n\n//     header, footer, form {\n//         display: none;\n//     }\n// }\n\n// @page {\n//     margin: 2cm;\n// }","@import 'base'; \n\n.section-container_trip-length {\n    grid-area: tl;\n    background-color: $trans-background_green;\n\n    #trip-length_header-wrapper {\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        text-align: center;\n        background-color: rgba(158, 19, 61, 0.3);\n        width: 100%;\n        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.8);\n    }\n\n    #trip-range {\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        transform: translateY(06vh);\n        width: 100%;\n        background-color: $trans-background_test-color;\n    }\n\n        #trip-length-list {\n            padding-left: 0;\n            width: 94%;\n\n            ul {\n                padding: 0;\n            }\n\n            li {\n                display: flex;\n                align-items: center;\n                justify-content: center;\n                box-shadow: $parchment-box-shadow;\n                width: 100%;\n   