gn: center;\n            z-index: 1;\n            background-color: transparent;\n\n        .countdown-quantifiers {\n            font: {\n                family: 'TerraIgnota'\n            }\n        }\n\n        #countdown-to-text {\n            font: {\n                family: 'Fredericka the Great';\n                size: 1.75em;\n            }\n        }\n\n        ul {\n            display: flex;\n            flex-direction: column;\n            justify-content: center;\n            padding: 0;\n            align-items: center;\n        }\n\n        ul.hidden {\n            display: none;\n        }\n    }\n\n        li {\n            background-color: $semi-trans_persian-green_no3;\n            border: none;\n            border-radius: 6px;\n        }\n        \n        .countdown-digits {\n            font: {\n                family: 'monospace';\n                // family: 'Fredericka the Great';\n                size: 3rem;\n            }\n            color: $main-dark;\n        }\n\n        #countdown-background {\n            background-color: $semi-trans_main-light;\n        }\n\n        #remaining-time {\n            ul {\n                padding:0;\n                margin: 0;\n            }\n        }\n}"],"sourceRoot":""}]);
