Fe,CCsFf,oBAAA,CACA,cAAA,CAGJ,+BACI,8BAAA,CACA,0BAAA,CACA,mBAAA,CACA,gBAAA,CACA,SAAA,CAEI,aAAA","sourcesContent":["/*------- FONT IMPORTS -------*/\n@import url('https://fonts.googleapis.com/css2?family=Fredericka+the+Great&family=Shadows+Into+Light+Two&display=swap');\n@font-face {\n    font-family: 'TerraIgnota';\n    src: url('./fonts/TerraIgnota.woff2') format('woff2'),\n      url('./fonts/TerraIgnota.woff') format('woff');\n    font-weight: 600;\n    font-style: normal;\n  }\n\n// ------------------------------ COLOR PALETTE -------------------//\n\n/* ------- SCSS RGB palette imported from Coolors.co* ------- */\n$charcoal: rgba(38, 70, 83, 1);\n$persian-green: rgba(42, 157, 143, 1);\n$maize-crayola: rgba(233, 196, 106, 1);\n$sandy-brown: rgb(194, 95, 14);\n$burnt-sienna: rgba(231, 111, 81, 1);\n\n// ------------ COLOR PALETTE VARIATIONS/CUSTOMIZATIONS ------------//\n$border-color: rgba(182, 182, 182, 0.4);\n$button-color: rgba(182, 182, 182, 0.6);\n$main-dark: rgb(0, 10, 6);\n$main-light: rgba(245, 245, 245, 0.9);\n\n$trans-background_aqua: rgba(119, 240, 224, 0.2);\n$trans-background_orange: rgba(255, 102, 0, 0.1);\n$trans-background_green: rgba(153, 252, 140, 0.1);\n$trans-background_charcoal: rgba(38, 70, 83, 0.1);\n$trans-background_persian-green: rgba(42, 157, 143, 0.1);\n$trans-background_maize-crayola: rgba(233, 196, 106, 0.1);\n$trans-background_sandy-brown: rgba(244, 162, 97, 0.1);\n$trans-background_burnt-sienna: rgba(231, 111, 81, 0.1);\n$trans-background_test-color: rgba(224, 118, 228, 0.1);\n\n$semi-trans_burnt-sienna: rgba(231, 111, 81, 0.8);\n$semi-trans_persian-green: rgba(92, 34, 0, 0.3);\n$semi-trans_persian-green_no2: rgba(0, 63, 92, 0.85);\n$semi-trans_persian-green_no3: rgba(0, 63, 92, 0.5);\n$semi-trans_main-light_more-trans: rgba(245, 245, 245, 0.1);\n$semi-trans_main-light: rgba(245, 245, 245, 0.9);\n$semi-trans_button-color: rgba(182, 182, 182, 0.3);\n$semi-trans_maize-crayola: rgba(233, 196, 106, 0.7);\n\n// ------- FORMAT VARIABLES ------- //\n$parchment-box-shadow: 1px 1px 12px 1px rgba(128, 128, 128, 0.4);\n\n// ----------- TYPOGRAPHY -----------//\n:root {\n    font: {\n        family: 'TerraIgnota', sans-serif;\n        size: 18px;\n    }\n}\n\nh1 {\n    font: {\n        family: 'Fredericka the Great';\n        color: $main-dark;\n        size: 64px;\n    }\n}\n\nh2, h3, h4 {\n    font-family: 'TerraIgnota';\n    color: $main-dark;\n}\n\n.wrapper {\n    background-image: url('../media/icons/oldtime_map_background.jpg');\n    background-size: cover;\n    min-height: 100vh;\n    overflow: hidden;\n}\n\nhtml, body {\n    margin: 0;\n    min-height: 100%;\n}\n\n* {\n    box-sizing: border-box;\n    border-radius: 6px;\n}\n\n// ----- PRINT FORMATTING ----- //\n// @media print {\n//     .section-container {\n//                 border: fuchsia;\n//                 color: fuchsia;\n//             }\n\n//     header, footer, form {\n//         display: none;\n//     }\n// }\n\n// @page {\n//     margin: 2cm;\n// }","@import 'base'; \n\n// ------------- SECTION CONTAINER ----------------//\n.section-container_pack-list {\n    grid-area: pl;\n    background-color: $trans-background_aqua;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n\n    li.checked-off {\n        color: $semi-trans_maize-crayola;\n        text-decoration: line-through;\n    }\n\nli {\n    -webkit-tap-highlight-color: transparent;\n}\n\n    #pack-list-btn {\n        min-width: 80%;\n        background-color: $semi-trans_persian-green_no2;\n        font: {\n            size: 1.5em;\n            family: 'Fredericka the Great', sans-serif;\n            weight: bold;\n        }\n        color: $main-light;\n    }\n    \n    #pack-list-btn:hover {\n            background-color: $semi-trans_persian-green;\n    }\n\n    #pack-list-instructions {\n        padding: 0;\n        max-width: 45%;\n\n        pre {\n            font: {\n                family: 'Fredericka the Great', sans-serif;\n                size: 1em;\n            }\n            word-wrap: break-word;\n            position: absolute;\n            margin-left: 45%;\n            margin-top: 10%;\n            color: $main-dark;\n        }\n    }\n\n    // ------------------ PACK LIST SECTION FORM ---------------------//\n    #pack-list-form {\n        box-shadow: none;\n\n        .input#input {\n            margin-left: 1em;\n            max-width: 45%;\n            background-color: $semi-trans_main-light;\n        }\n        \n        .input#input:focus {\n            outline-color: $maize-crayola;\n            outline: {\n                border-radius: 16px;\n            }\n        }\n    }            \n}\n\n// --------------------- MODAL RULES ------------------//\n.modal {\n    display: none; /* Hidden by default */\n    position: fixed; \n    z-index: 1; \n    padding-top: 5vh; \n    left: 0;\n    top: 0;\n    width: 100vw; \n    height: 100vh; \n    margin: 0;\n    overflow: auto; \n    background-color: rgba(0,0,0,0.75); \n  }\n  \n  /* --------- Packing list contents -------------- */\n.modal-content { \n    margin: auto;\n    background-image: url('../media/icons/vintage-notebook_CROPPED.jpg');\n    background-size: cover;\n    background-repeat: no-repeat;\n    padding: 0.25em;\n    border: 1px solid #888;\n    overflow: hidden;\n    width: 80%;\n    height: 100%;\n    justify-content: center;\n    align-items: center;\n    overflow: scroll;\n\n    h3 {\n        margin-left: 2em;\n        font-size: 3em;\n        padding: 0;\n        margin:  0 0 0 1em;\n    }\n\n    .close {\n        position: relative;\n        color: $main-dark;\n        font-size: 2em;\n        padding: 0.25em;\n        float: right;\n        border: solid rgba(26, 26, 26, 0.8);\n        border-width: 2px;\n    }\n\n        .close:hover,\n        .close:focus {\n            background-color: $trans-background_persian-green;\n            color: $main-light;\n            border-color: $semi-trans_persian-green;\n            text-decoration: none;\n            cursor: pointer;\n        }\n\n        #pack-list-item {\n            background-color: transparent;\n            border-color: transparent;\n            padding: 0 0 0 1.5em;\n            margin-left: 1rem;\n            width: 45%;\n            font: {\n                size: 2em;\n            }\n        }\n}"],"sourceRoot":""}]);
