----- COLOR PALETTE -------------------//\n\n/* ------- SCSS RGB palette imported from Coolors.co* ------- */\n$charcoal: rgba(38, 70, 83, 1);\n$persian-green: rgba(42, 157, 143, 1);\n$maize-crayola: rgba(233, 196, 106, 1);\n$sandy-brown: rgb(194, 95, 14);\n$burnt-sienna: rgba(231, 111, 81, 1);\n\n// ------------ COLOR PALETTE VARIATIONS/CUSTOMIZATIONS ------------//\n$border-color: rgba(182, 182, 182, 0.4);\n$button-color: rgba(182, 182, 182, 0.6);\n$main-dark: rgb(0, 10, 6);\n$main-light: rgba(245, 245, 245, 0.9);\n\n$trans-background_aqua: rgba(119, 240, 224, 0.2);\n$trans-background_orange: rgba(255, 102, 0, 0.1);\n$trans-background_green: rgba(153, 252, 140, 0.1);\n$trans-background_charcoal: rgba(38, 70, 83, 0.1);\n$trans-background_persian-green: rgba(42, 157, 143, 0.1);\n$trans-background_maize-crayola: rgba(233, 196, 106, 0.1);\n$trans-background_sandy-brown: rgba(244, 162, 97, 0.1);\n$trans-background_burnt-sienna: rgba(231, 111, 81, 0.1);\n$trans-background_test-color: rgba(224, 118, 228, 0.1);\n\n$semi-trans_burnt-sienna: rgba(231, 111, 81, 0.8);\n$semi-trans_persian-green: rgba(92, 34, 0, 0.3);\n$semi-trans_persian-green_no2: rgba(0, 63, 92, 0.85);\n$semi-trans_persian-green_no3: rgba(0, 63, 92, 0.5);\n$semi-trans_main-light_more-trans: rgba(245, 245, 245, 0.1);\n$semi-trans_main-light: rgba(245, 245, 245, 0.9);\n$semi-trans_button-color: rgba(182, 182, 182, 0.3);\n$semi-trans_maize-crayola: rgba(233, 196, 106, 0.7);\n\n// ------- FORMAT VARIABLES ------- //\n$parchment-box-shadow: 1px 1px 12px 1px rgba(128, 128, 128, 0.4);\n\n// ----------- TYPOGRAPHY -----------//\n:root {\n    font: {\n        family: 'TerraIgnota', sans-serif;\n        size: 18px;\n    }\n}\n\nh1 {\n    font: {\n        family: 'Fredericka the Great';\n        color: $main-dark;\n        size: 64px;\n    }\n}\n\nh2, h3, h4 {\n    font-family: 'TerraIgnota';\n    color: $main-dark;\n}\n\n.wrapper {\n    background-image: url('../media/icons/oldtime_map_background.jpg');\n    background-size: cover;\n    min-height: 100vh;\n    overflow: hidden;\n}\n\nhtml, body {\n    margin: 0;\n    min-height: 100%;\n}\n\n* {\n    box-sizing: border-box;\n    border-radius: 6px;\n}\n\n// ----- PRINT FORMATTING ----- //\n// @media print {\n//     .section-container {\n//                 border: fuchsia;\n//                 color: fuchsia;\n//             }\n\n//     header, footer, form {\n//         display: none;\n//     }\n// }\n\n// @page {\n//     margin: 2cm;\n// }","@import 'base';\n\n.wrapper {\n    padding: 0;\n    display: grid;\n    grid-gap: 2px;\n    grid-template-columns: repeat(12, 1fr);\n    grid-template-rows: 80px auto auto 140px;\n    grid-template-areas:\n    \"hd hd hd hd hd hd hd hd hd hd hd hd\"\n    \"ef ef ef ef ef ef ef ef ef ef ef ef\"\n    \"if if if if if if cd cd cd cd cd cd\"\n    \"if if if if if if cd cd cd cd cd cd\"\n    \"cf cf cf cf wr wr wr wr tl tl tl tl\"\n    \"cf cf cf cf wr wr wr wr pl pl pl pl\"\n    \"f f f f f f f f f f f f\";\n}\n\n// ------------------ TYPOGRAPHY -------------------------//\nh1 {\n    font: {\n        size: 4rem;\n        family: 'Fredericka the Great';\n    }\n    margin: 0;\n    padding-left: 1rem;\n    padding-bottom: 0.5rem;\n}\n\nh2 {\n    font-size: 3.5rem;\n    margin: 0;\n}\n\nh3 {\n    padding: 0.75em;\n\n    font: {\n        family: 'Fredericka the Great', sans-serif;\n    }\n}\n\nh4 {\n    font-size: 3em;\n    margin: 0.25em;\n}\n\n// ------------------------ GLOBAL STYLE/FORMAT -------------------------//\nbody {\n    background-color: $main-light;\n    width: 100vw;\n    margin: 0;\n}\n\ndiv {\n    padding: 1em;\n}\n\nsection {\n    background-color: rgba(245, 245, 245, 0.6);\n    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.8);\n}\n\nform {\n    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);\n    max-width: 100%;\n    justify-content: center;\n    align-items: center;\n    padding: 0.5rem;\n    list-style-type: none;\n}\n\nul {\n    display: flex;\n    flex-direction: column;\n    list-style-type: none;\n    justify-content: center;\n}\n\nli {\n    background-color: $main-light;\n    margin: 0;\n    list-style-type: none;\n    cursor: pointer;\n    border-top: 1px solid #e5e5e5;\n    font-size: 1.5rem;\n    padding: 1rem 2rem;\n    border-radius: 4px;\n}\n\nbutton {\n    padding: 1em;\n    min-width: 300px;\n    border: none;\n    border-radius: 4px;\n    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.6);\n    background-color: $button-color;\n    transition: ease-out 0.4s;\n    font: {\n        family: 'Fredericka the Great', sans-serif;\n        size: 1em;\n    }\n}\n\nbutton:hover {\n    color: $main-dark;\n    background-color: $main-light;\n    box-shadow: 0 12px 12px rgba(0, 0, 0, 0.95);\n    transition: ease-in 0.2s;\n}\n\n// -------------------- STATE-SPECIFIC-GLOBAL STYLE/FORMAT ---------------------------//\n.hidden {\n    display: none;\n}\n\n.input {\n    border: none;\n    color: #444;\n    font-size: 2rem;\n    padding: 1rem;\n    display: block;\n    margin: auto;\n    width: 60%;\n    font: {\n        family: 'TerraIgnota', sans-serif;\n    }\n}\n\n.input::placeholder {\n    color: rgba(143, 94, 31, 0.4);\n}\n\n.input:focus {\n    outline: 6px solid $maize-crayola;\n}"],"sourceRoot":""}]);
